@import 'vars.scss';

body.page {
	overflow-y: scroll;
	background-color: white;
	color: black;
	header {
		background-color: $orange;
		pointer-events: all;
		position: static;
	}
}

h2 {
	@include xl-text;
	text-transform: lowercase;
	margin: 0 0 20px 0;
}

#page-content {
	background-color: white;
}


section {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	padding: 0 160px;
	@include lg-under {
		padding: 0;
	}
}

.col {
	padding: 80px 0;
	position: relative;
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	&:first-child {
		// margin-right: 12.5%;
		margin-right: 100px;
	}
	&.narrow {
		width: 400px;
		min-height: 400px;
		box-sizing: content-box;
	}
	&.wide {
		width: 50%;
		max-width: 800px;
		flex: 1;
	}
}

#about-site {
	.col.narrow {
		background-position-y: top;
		margin-bottom: 80px;
	}
}


#about-resources {
	background-color: $lightgreen;
	color: white;
}

section.worker {
	margin-bottom: 80px;
	.col.narrow {
		position: relative;
		padding-bottom: 160px;
		&::after {
			content: '';
			width: 100%;
			height: 3%;
			background-color: $yellow;
			position: absolute;
			top: 100%;
			left: 0;
		}
	}
	.col-wide {
		display: flex;
    flex-direction: column;
    justify-content: center;
	}
	&:nth-child(1) .col.narrow::after {
		transform: rotate(5deg);
	}
	&:nth-child(2) .col.narrow::after {
		transform: rotate(-4deg);
	}
	&:nth-child(3) .col.narrow::after {
		transform: rotate(3deg);
	}
	&:nth-child(4) .col.narrow::after {
		transform: rotate(-5deg);
	}
	&:nth-child(5) .col.narrow::after {
		transform: rotate(2deg);
	}
	&:last-child .col.narrow::after {
		display: none;
	}
}

footer {
	min-height: 378px;
	background-color: $lightgreen;
	color: white;
	@include xs-text;
	overflow: hidden;
}
#footer-inner {
	max-width: $lg-width;
	padding: 64px 24px;
	margin: auto;
	display: flex;
	a {
		text-decoration: none;
		font-weight: bold;
	}
}
#footer-text {
	max-width: 536px;
}
#footer-caduceus {
	flex: 0 0 380px;
	position: relative;
	img {
		width: 100%;
		opacity: 0.18;
		transform: translate(5%, 5%) scale(1.1);
		position: absolute;
		left: 0;
		top: -35px;
	}
	@include md-under {
		display: none;
	}
}
